import React from "react";
import ReactTable from "react-table";
import { CSVLink, CSVDownload } from "react-csv";


const NullComponent = () => {
    return(
        <div>
            <p className="no-margin text-center" style={{paddingTop:10, color:'#656d83'}}>No data found</p>
        </div>
    )
};

export default function CommonReactTable ({row_data,column_data,showPag,minRow,resizable,defaultSorted,defaultPageSize,sortable,exportCSV,csvFile,csvData}) {    
    

    return (
        <>
            {exportCSV &&
                <div className='text-right'>
                    <CSVLink  filename={`${csvFile}.csv`} data={csvData} className="btn btn-secondary btn-std">Export as CSV</CSVLink>	
                </div> 
            }            
            <div className={`table-responsive ${row_data.length === 0 ? 'no_scroll' : ''} customer_list_reactTable border_bot`}>
                <ReactTable NoDataComponent={NullComponent} showPaginationBottom={showPag && row_data.length > 0 && true} minRows = {minRow}  data={row_data}  
                columns={column_data} resizable={resizable} sortable={sortable} defaultPageSize={defaultPageSize ? defaultPageSize : 10}  defaultSorted={defaultSorted}  />
            </div>
        </>
    )
}